import React from "react";
import Products from "./Products";
import TopMenu from "./TopMenu";
import FadingScreen from "./FadingScreen";
import Footer from "./Footer";
// import ObserverWrapper from "./ObserverWrapper";

function Starter() {
  const handleScrollYDir = () => {
    if (!show) {
      if (window.pageYOffset > 660) {
        setShow(true);
      }
    }
  };
  const handleScrollXDir = e => {
    if (e.target.scrollLeft > 100) {
      setDarken("fade-out");
    } else {
      setDarken("fade-in");
    }
    console.log("darken", darken);
    console.log("scroll", e.target.scrollLeft);
  };
  const [show, setShow] = React.useState(false);
  const DanceList = [
    {
      id: "dance1",
      stars: "3",
      description: "Online Dancing Class",
      name: "Jazz"
    },
    { id: "dance2", stars: "5", description: "Classic", name: "Tango" },
    {
      id: "dance3",
      stars: "2",
      description: "For Teenagers",
      name: "Ballet"
    },
    { id: "dance4", stars: "5", description: "All Ages", name: "Zumba" }
  ];
  const [ProductList, setProductList] = React.useState([
    {
      id: "product1",
      stars: 1,
      description: "All kinds of skin",
      name: "Skin Cream"
    },
    {
      id: "product2",
      stars: 4,
      description: "Classy",
      name: "Lipstick"
    },
    {
      id: "product3",
      stars: "3",
      description: "Old Ladies",
      name: "Eye Cream"
    },
    {
      id: "product4",
      stars: "5",
      description: "Young Ladies",
      name: "Eye Cream"
    }
  ]);
  const [lastYPos, setLastYPos] = React.useState(0);
  const [darken, setDarken] = React.useState("fade-in");
  console.log("window.pageYOffset", window.pageYOffset);
  React.useEffect(() => {
    function handleScroll() {
      const yPos = window.scrollY;

      setLastYPos(yPos);
    }

    window.addEventListener("scroll", handleScroll, false);

    return () => {
      window.removeEventListener("scroll", handleScroll, false);
    };
  }, [lastYPos]);

  return (
    <div className="cover">
      <TopMenu />

      <FadingScreen subject="beauty" yPosition="400" />

      <div onScroll={e => handleScrollXDir(e)} className="insider">
        <div className={darken}>
          <b className="bold-title">
            Discover <br />
            Our <br />
            Beauty Products
          </b>
        </div>
        {ProductList.map((ProductList, index) => {
          return (
            <Products
              key={index}
              product={ProductList.id}
              stars={ProductList.stars}
              description={ProductList.description}
              name={ProductList.name}
            />
          );
        })}
        <div className="empty"></div>
      </div>
      {handleScrollYDir()}
      <div className="fitness-container">
        {show ? <FadingScreen subject="fitness" yPosition="1100" /> : null}
      </div>
      <div
        onScroll={e => handleScrollXDir(e)}
        style={{
          marginTop: "0",
          padding: "0"
        }}
        className="insider"
      >
        <div
          style={{ marginTop: "-150px", paddingTop: "0" }}
          className={darken}
        >
          <b className="bold-title">
            Find <br />
            Where Your Heart <br />
            Truly Lies
          </b>
        </div>
        {DanceList.map((DanceList, index) => {
          return (
            <Products
              key={index}
              product={DanceList.id}
              stars={DanceList.stars}
              description={DanceList.description}
              name={DanceList.name}
            />
          );
        })}
        <div className="empty"></div>
      </div>
      <Footer />
    </div>
  );
}

export default Starter;
