import React from "react";
import Women from "../pictures/Women.png";
import Modal from "./Modal";
import { withRouter } from "react-router-dom";
class TopMenu extends React.Component {
  constructor() {
    super();
    this.state = {
      Status: false
    };
  }
  render() {
    return (
      <>
        {this.state.Status && (
          <Modal closeModal={() => this.setState({ Status: false })}>
            <span>The content of the modal</span>
            <span>The content of the modal</span>
            <span>The content of the modal</span>

            <span>The content of the modal</span>

            <span>The content of the modal</span>
          </Modal>
        )}
        <div className="top-menu">
          <img src={Women} alt="Women's beauty" />
          <span onClick={() => this.setState({ Status: true })}>MAKE UP</span>
          <span>SKIN CARE</span>
          <span onClick={() => this.props.history.push("/products")}>HAIR</span>
          <span>MEN</span>
          <span>VIRTUAL TRY ON</span>
          <span>BEAUTY MAGAZINE</span>
        </div>
      </>
    );
  }
}
export default withRouter(TopMenu);
